.footer-container {
  background: rgba(28, 34, 55, 1);
  padding: 20px 15px 5px 20px;
  position: relative;
}

.footer-content {
  display: flex;
  justify-content: space-between;
  padding: 20px 5px;
  align-items: center;
}

.footer-logo {
  color: #fff;
  justify-self: start;
  cursor: pointer;
  text-decoration: none;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
}

.footer-socials {
  display: flex;
  gap: 20px;
}

.footer-logo-icon {
  margin-right: 0.5rem;
  font-size: 1.5rem;
}

.footer-icon {
  font-size: 1.5rem;
  transition: all 0.3s ease;
}

.footer-icon:hover {
  fill: #f00946;
  transform: scale(1.3);
}

@media screen and (min-width: 960px) {
  .footer-content {
    margin-top: 20px;
    flex-direction: column;
    gap: 10px;
  }

  .footer-logo {
    font-size: 4rem;
  }

  .footer-logo-icon {
    margin-right: 0.5rem;
    font-size: 4rem;
  }

  .footer-icon {
    font-size: 3rem;
    transition: all 0s ease;
    cursor: pointer;
  }

  .footer-socials {
    gap: 70px;
  }

  .footer-icon:hover {
    fill: #f00946;
    transform: scale(1);
  }
}