.navbar {
  background: rgba(28, 34, 55, 0.7);
  min-height: 7vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 5;
  backdrop-filter: blur(50px);
  -webkit-backdrop-filter: blur(50px);
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  min-height: 7vh;
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.navbar-icon {
  margin-right: 0.5rem;
  font-size: 2rem;
}

.nav-menu {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  text-align: center;
  justify-content: end;
  gap: 50px;
}

.nav-item {
  height: 80px;
  border-bottom: 2px solid transparent;
}

.nav-item:hover {
  border-bottom: 2px solid #f00946;
}

.nav-icon:hover {
  fill: #f00946;
}

.nav-icon {
  min-width: 50px;
  transform: scale(1.5);
  cursor: pointer;
}

.nav-socials {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80px;
  gap: 10px;
}

.nav-link {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  font-size: 1.5rem;
  height: 100%;
}

.fa-bars {
  color: #fff;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .navbar.active {
    background: rgba(28, 34, 55, 1);
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 93vh;
    position: absolute;
    justify-content: center;
    top: 7vh;
    left: -100%;
    opacity: 6;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: rgba(28, 34, 55, 1);
    left: 0;
    opacity: 1;
    transition: all 0.6s ease;
    z-index: 6;
  }

  .nav-link {
    text-align: center;
    padding: 1.5rem;
    display: table;
    margin-left: auto;
    margin-right: auto;
  }

  .nav-link:hover, .nav-icon:hover {
    color: #f00946;
    transform: scale(1.3);
    transition: all 0.3s ease;
  }

  .nav-icon:hover {
    fill: #f00946;
  }

  .nav-item:hover {
    border: none;
  }

  .nav-socials {
    width: 100%;
    justify-content: space-evenly;
  }

  .nav-icon {
    width: 10%;
    height: auto;
    transform: scale(1);
  }

  .nav-item {
    width: 100%;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(10%, 20%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 30%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }
}