.about-container {
  background: rgba(28, 34, 55, 1);
  padding: 30px 15px;
  position: relative;
}

.about-title {
  text-align: center;
  font-size: 2rem;
}

.about-container p {
  font-size: 1rem;
  text-align: center;
  margin: 15px 15px;
}

.about-sections {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.about-section {
  position: relative;
  display: inline-block;
  padding: 25px;
  margin: 15px 0;
  color: white;
  text-decoration: none;
  transition: 0.5s;
  overflow: hidden;
  width: 100%;
}

.about-section.purple {
  border: 6px solid #fb84fb;
}
.about-section.blue {
  border: 6px solid #03e9f4;
}
.about-section.green {
  border: 6px solid #73c903;
}

.section-body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
}

.section-text {
  text-align: center;
}

.section-title {
  font-size: 1.5rem;
  margin-bottom: 20px;
}

@media screen and (min-width: 960px) {
  .about-container {
    padding-right: 150px;
    padding-left: 150px;
  }

  .about-title {
    text-align: center;
    font-size: 5rem;
  }

  .about-container p {
    font-size: 2rem;
    margin: 55px 55px;
  }

  .about-section {
    padding: 35px;
    margin: 15px 0;
    width: 70%;
  }

  .section-body {
    flex-direction: row;
  }

  .section-body.section-reverse {
    flex-direction: row-reverse;
  }

  .section-title {
    font-size: 2.5rem;
    margin-bottom: 20px;
  }

  .section-img {
    flex: 0 0 50%;
  }

  .section-img img{
    width: 100%;
  }
}