.roadmap-container {
  background: rgba(28, 34, 55, 1);
  padding: 20px 15px;
  position: relative;
}

.roadmap-title {
  text-align: center;
  font-size: 2rem;
}

.roadmap-timeline {
  position: relative;
  margin-top: 30px;
}

.roadmap-timeline::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 0;
  width: 6px;
  background: #18012c;
  height: 100%;
  margin-left: -1px;
  background-color: #FFFFFF;
}

.timeline-element {
  padding: 80px 0 0;
  display: flex;
  justify-content: space-between;
  position: relative;
}

.timeline-element.timeline-reverse {
  flex-direction: row-reverse;
}

.timeline-element.timeline-filled::before {
  opacity: 1;
}

.timeline-element.timeline-filled .element-date::before {
  border-color: #fff;
  background-color: transparent;
  background-image: linear-gradient(to left, #fb84fb, #03e9f4, #73c903);
  background-clip: content-box;
  -webkit-background-clip: content-box;
  animation: color-animate 5s linear infinite;
  -webkit-background-size: 800%;
  background-size: 800%;
}

.timeline-element::before {
  position: absolute;
  content: "";
  width: 6px;
  height: 100%;
  left: 50%;
  transform: translateX(-1px);
  top: 0;
  transition: 0.4s ease-in-out;
  opacity: 0;
  background-color: transparent;
  background-image: linear-gradient(to left, #fb84fb, #03e9f4, #73c903);
  background-clip: content-box;
  -webkit-background-clip: content-box;
  animation: color-animate 5s linear infinite;
  -webkit-background-size: 800%;
  background-size: 800%;
}

@keyframes color-animate {
  0% {
    background-position: 0 100%;
  }
  50% {
    background-position: 100% 0;
  }
  100% {
    background-position: 0 100%;
  }
}

.element-date {
  position: relative;
  width: 50%;
  text-align: right;
  align-self: baseline;
  position: relative;
  padding-right: 40px;
  padding-top: 3px;
  font-size: 1.2rem;
  font-weight: 700;
}

.element-event {
  padding-left: 40px;
  text-align: left;
  width: 50%;
  position: relative;
  font-size: 0.9rem;
}

.element-date::before {
  position: absolute;
  content: "";
  right: -12px;
  top: 50%;
  margin-top: 0px;
  height: 20px;
  width: 20px;
  border: 6px solid #fff;
  transform: translateY(-50%);
  transition: 0.4s ease-in-out;
  background: rgba(28, 34, 55, 1);
}

.timeline-reverse .element-date::before {
  right: auto;
  left: -8px;
}

.timeline-reverse .element-date {
  padding-right: 0;
  padding-left: 40px;
  text-align: left;
}

.timeline-reverse .element-event {
  padding-left: 0;
  padding-right: 40px;
  text-align: right;
}

@media screen and (min-width: 960px) {
  .roadmap-title {
    font-size: 5rem;
  }

  .roadmap-timeline::before {
    width: 16px;
    margin-left: -6px;
  }

  .timeline-element::before {
    width: 16px;
    transform: translateX(-6px);
  }

  .element-date::before {
    right: -18px;
    height: 32px;
    width: 32px;
    border: 8px solid #fff;
    transform: translateY(-70%);
  }

  .timeline-reverse .element-date::before {
    left: -14px;
  }

  .element-date {
    font-size: 2.2rem;
  }
  
  .element-event {
    padding-left: 40px;
    font-size: 1.2rem;
  }

  .timeline-element {
    padding: 110px 0 0;
  }
}