.banner-fullscreen {
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.fullscreen-body {
  background-color: rgba(0, 0, 0, 0.5);
  padding: 50px 15px;
  position: relative;
  z-index: 2;
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
}

.play-button {
  position: relative;
  display: inline-block;
  padding: 25px 80px;
  margin: 20px 0;
  color: #03e9f4;
  text-decoration: none;
  text-transform: uppercase;
  transition: 0.5s;
  letter-spacing: 4px;
  overflow: hidden;
  border: none; 
  background: none;
  font-size: 1.5rem;
  filter: hue-rotate(270deg);
  cursor: pointer;
}

.play-button:hover {
  background: #03e9f4;
  color: #050801;
  box-shadow: 0 0 5px #03e9f4,
              0 0 25px #03e9f4,
              0 0 50px #03e9f4,
              0 0 200px #03e9f4;
   -webkit-box-reflect:below 1px linear-gradient(transparent, #0005);
}

.play-button span {
  position: absolute;
  display: block;
}

.play-button span:nth-child(1){
  top: 0;
  left: 0;
  width: 100%;
  height: 6px;
  background: linear-gradient(90deg,transparent,#03e9f4);
  animation: animate1 1s linear infinite;
}

@keyframes animate1 {
  0%{
      left: -100%;
  }
  50%,100%{
      left: 100%;
  }
}

.play-button span:nth-child(2) {
  top: -100%;
  right: 0;
  width: 6px;
  height: 100%;
  background: linear-gradient(180deg,transparent,#03e9f4);
  animation: animate2 1s linear infinite;
  animation-delay: 0.25s;
}

@keyframes animate2 {
  0%{
      top: -100%;
  }
  50%,100%{
      top: 100%;
  }
}

.play-button span:nth-child(3) {
  bottom: 0;
  right: 0;
  width: 100%;
  height: 6px;
  background: linear-gradient(270deg,transparent,#03e9f4);
  animation: animate3 1s linear infinite;
  animation-delay: 0.50s;
}

@keyframes animate3 {
  0%{
      right: -100%;
  }
  50%,100%{
      right: 100%;
  }
}

.play-button span:nth-child(4){
  bottom: -100%;
  left: 0;
  width: 6px;
  height: 100%;
  background: linear-gradient(360deg,transparent,#03e9f4);
  animation: animate4 1s linear infinite;
  animation-delay: 0.75s;
}

@keyframes animate4 {
  0%{
      bottom: -100%;
  }
  50%,100%{
      bottom: 100%;
  }
}

.fullscreen-title {
  text-align: center;
  font-size: 2.3rem;
  font-weight: 700;
  color: white;
  margin: auto;
}

.fullscreen-title span {
  font-size: 3rem;
  color: transparent;
  background-image: linear-gradient(to left, #fb84fb, #03e9f4, #73c903);
  background-clip: text;
  -webkit-background-clip: text;
  animation: color-animate 5s linear infinite;
  -webkit-background-size: 500%;
  background-size: 500%;
}

@keyframes color-animate {
  0% {
    background-position: 0 100%;
  }
  50% {
    background-position: 100% 0;
  }
  100% {
    background-position: 0 100%;
  }
}

.fullscreen-text {
  font-size: 1.3rem;
  text-align: center;
  color: white;
  font-weight: 700;
}

.fullscreen-video {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media screen and (min-width: 960px) {
  .fullscreen-title {
    font-size: 5rem;
    font-weight: 900;
    padding: 100px;
  }
  
  .fullscreen-title span {
    font-size: 7rem;
    display: block;
  }
  
  .fullscreen-text {
    font-size: 3rem;
    font-weight: 900;
  }

  .play-button {
    font-size: 2rem;
  }
}