*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT SANS', sans-serif;
  font-size: 1.2rem;
}

html, body {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;  
  scroll-padding-top: 7vh;
}

.container {
  z-index: 1;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  padding-right: 50px;
  padding-left: 50px;
  overflow: hidden;
  color: white;
}

@media screen and (max-width: 960px) {
  html {
    font-size: 120%;
  }
}
@media screen and (max-width: 360px) {
  html {
    font-size: 80%;
  }
}